<template lang="html">
  <div class="editor-component">
    <div ref="toolbar" class="editor-component-toolbar" v-show="showHeader"></div>
    <div ref="editor" class="editor-component-text"></div>
  </div>
</template>

<script>
import E from "wangeditor";
export default {
  name: "EditorComponent",
  data() {
    return {
      // uploadPath,
      editor: null,
      info_: null
    };
  },
  model: {
    prop: "value",
    event: "change"
  },
  props: {
    value: {
      type: String,
      default: ""
    },
    placeValue: {
      type: String,
      default: ""
    },
    showHeader: {
      type: Boolean,
      default: true
    },
    menu: {
      default: []
    },
    isClear: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    isClear(val) {
      // 
      if (val) {
        this.editor.txt.clear();
        this.info_ = null;
      }
    },
    value: function(value) {
      if (value !== this.editor.txt.html()) {
        this.editor.txt.html(this.value);
      }
    }
    //valuevalue
  },
  mounted() {
    this.seteditor();
    this.editor.txt.html(this.value);
  },
  methods: {
    seteditor() {
      var self = this;
      // 
      this.editor = new E(this.$refs.toolbar, this.$refs.editor);
      if (this.menu.length > 0) {
        this.editor.config.menus = this.menu;
      } else {
        this.editor.config.menus = [
          "head", // 
          "bold", // 
          "fontSize", // 
          "fontName", // 
          "italic", // 
          "underline", // 
          "strikeThrough", // 
          "foreColor", // 
          "backColor", // 
          "link", // 
          "list", // 
          "justify", // 
          "quote", // 
          "emoticon", // 
          "image", // 
          "table", // 
          "video", // 
          "code", // 
          "undo", // 
          "redo", // 
          "fullscreen" // 
        ];
      }
      this.editor.config.placeholder = this.placeValue;
      this.editor.config.uploadImgShowBase64 = false;
      this.editor.config.showLinkImg = false;
      this.editor.config.uploadImgServer = "http://localhost:3000/image";
      // this.editor.customConfig.uploadImgShowBase64 = false; // base 64 
      // this.editor.customConfig.uploadImgServer =
      //   "http://otp.cdinfotech.top/file/upload_images"; // 
      // this.editor.customConfig.uploadImgHeaders = {}; //  header
      // this.editor.config.uploadFileName = "file"; // 
      this.editor.config.uploadImgMaxSize = 10 * 1024 * 1024; //  2M
      this.editor.config.uploadImgMaxLength = 1; //  3 
      this.editor.config.uploadImgTimeout = 3 * 60 * 1000; // 

      this.editor.config.customUploadImg = function(files, insert) {
        console.log(files);
        if (files[0]) {
          const formData = new window.FormData();
          formData.append("file", files[0], "cover.jpg");
          self.$jq.ajax({
            url: "http://localhost:3000/image",
            type: "post",
            contentType: false,
            data: formData,
            processData: false,
            success: res => {
              console.log(res);
              if (res.flag) {
                insert(res.path);
              }
            },
            error: err => {
              console.log(err);
            }
          });
        } else {
          console.log("hhhh");
        }
      };


      this.editor.config.onchange = html => {
        this.info_ = html; // 
        this.$emit("change", this.info_); // 
      };

      console.log(this.menu);
      // 
      this.editor.create();
    }
  }
};
</script>

<style lang="css">
.editor-component {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 0;
}
.editor-component-toolbar {
  border: 1px solid #ccc;
}
.editor-component-text {
  min-height: 200px;
  height: calc(100% - 50px);
  border: 0;
}
</style>
