<template lang="html">
  <div class="comment-editor-component">
    <div
      ref="toolbar"
      class="editor-component-toolbar"
      v-show="showHeader"
    ></div>
    <div ref="editor" class="editor-component-text"></div>
  </div>
</template>

<script>
import E from "wangeditor";
export default {
  name: "CommentEditor",
  data() {
    return {
      // uploadPath,
      editor: null,
      info_: null
    };
  },
  model: {
    prop: "value",
    event: "change"
  },
  props: {
    value: {
      type: String,
      default: ""
    },
    placeValue: {
      type: String,
      default: ""
    },
    showHeader: {
      type: Boolean,
      default: true
    },
    isClear: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    isClear(val) {
      // 
      if (val) {
        this.editor.txt.clear();
        this.info_ = null;
      }
    },
    value: function(value) {
      if (value !== this.editor.txt.html()) {
        this.editor.txt.html(this.value);
      }
    }
    //valuevalue
  },
  mounted() {
    this.seteditor();
    this.editor.txt.html(this.value);
  },
  methods: {
    seteditor() {
      // 
      this.editor = new E(this.$refs.toolbar, this.$refs.editor);
      this.editor.config.menus = [
        "head", // 
        "bold", // 
        "fontSize", // 
        "fontName", // 
        "italic", // 
        "underline", // 
        "strikeThrough", // 
        "foreColor", // 
        "backColor", // 
        "link"
      ];
      this.editor.config.placeholder = this.placeValue;
      this.editor.config.uploadImgShowBase64 = false;
      this.editor.config.showLinkImg = false;
      this.editor.config.uploadImgServer = "http://localhost:3000/image";
      this.editor.config.uploadImgMaxSize = 10 * 1024 * 1024; //  2M
      this.editor.config.uploadImgMaxLength = 1; //  3 
      this.editor.config.uploadImgTimeout = 3 * 60 * 1000; // 
      this.editor.config.onchange = html => {
        this.info_ = html; // 
        this.$emit("change", this.info_); // 
      };

      console.log(this.menu);
      // 
      this.editor.create();
    }
  }
};
</script>

<style lang="css">
.comment-editor-component {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 0;
}
.editor-component-toolbar {
  border: 1px solid #ccc;
}
.editor-component-text {
  min-height: 40px;
  border: 0;
}
</style>
