import Jquery from "jquery";
import Vue from 'vue';
(function($) {
  //1.$.ajax
  var _ajax = $.ajax;
  $.ajax = function(options) {
    //4.$.ajax
    var _options = $.extend(options, {
      url: options.url,
      headers: {
        // 'User-Token': localStorage.getItem('uuid')
      },
      dataType:'json',
    });
    //5.ajax
    _ajax(_options);
  };
})(Jquery);
let jqueryPlugin = {};
jqueryPlugin.install = function(vue) {
  vue.prototype.$jq = Jquery;
}

Vue.use(jqueryPlugin);
